import { latLng, latLngBounds } from "leaflet";
export const maps = {
  globe: latLngBounds(latLng(80, 70), latLng(-60, -130)),
  northAmerica: latLngBounds(latLng(50, -74), latLng(20, -120)),
  sourthAmerica: latLngBounds(latLng(27, -8), latLng(-56, -110)),
  europe: latLngBounds(latLng(74, 66), latLng(16, -34)),
  africe: latLngBounds(latLng(45, 72), latLng(-40, -26)),
  asia: latLngBounds(latLng(82, 180), latLng(-32, 5)),
  australia: latLngBounds(latLng(30, 180), latLng(-53, 84)),
};
const stateTemplate = {
  bounds: null,
  zoom: null,
  center: null,
  duration: 2,
  date: new Date(),
};

const map = {
  state: {
    current: { ...stateTemplate},
    new: { ...stateTemplate, bounds: maps.globe },
  },
  getters: {
    mapGetZoom: (state) => {
      return state.current.zoom;
    },
    mapGetCenter: (state) => {
      return state.current.center;
    },
    mapGetBounds: (state) => {
      return state.current.bounds;
    },
    mapGetBoundsNew: (state) => {
      return state.new;
    },
    mapGetBoundsCurrent: (state) => {
      return state.current;
    },
  },
  mutations: {
    resetMap: (state) => {
      state.new = {
        ...stateTemplate, 
        bounds: maps.globe,
        date: new Date(),
      };
    },
    mapSetZoom: (state, zoom) => {
      state.new.zoom = zoom;
    },
    mapSetCenter: (state, center) => {
      state.new.center = center;
    },
    mapSetBounds: (state, bounds) => {
      state.new = {
        ...state.current,
        bounds: bounds,
        duration: 1,
        date: new Date(),
      };
    },
    /// the curent functions are for internal to the map
    // this allows the map to be responsive
    mapSetZoomCurrent: (state, zoom) => {
      state.current.zoom = zoom;
    },
    mapSetCenterCurrent: (state, center) => {
      state.current.center = center;
    },
    mapSetBoundsCurrent: (state, bounds) => {
      state.current.bounds = bounds;
    },
  },
  actions: {},
};

export default map;
